import React, { Component } from 'react';
import './index.css'
class formulaire extends Component{
    render() {
    var curr = new Date();
    var date = curr.toISOString().substr(0,10);
   
    return(
        <div className="formu">
            <h1> Formulaire</h1>
          <div className="main">
            <form>
                <fieldset>
                <legend>Identité</legend>

                    <h4 className="civilite"> Civilité:</h4>
                    <select className="option" name="civilité">
                        <option disabled="disabled" selected="selected">-- Choisir votre état civil --</option>
                        <option>Mademoiselle</option>
                        <option>Madame</option>
                        <option>Monsieur</option>
                    </select><br></br>


                    <div id="name">
                        <h4 className="name"> Nom et prénom:</h4>
                        <input className="nom" name="first_name" placeholder="Entrez votre nom"/><br></br>
                        <label className="firstlabel">Nom de famille</label>
                        <input className="pre" name="last_name" placeholder="Entrez votre prénom"/>
                        <label className="lastlabel">Prénom</label>
                    </div><br></br>

                    <h4 className="sexe">Sexe</h4>
                    <label className="radio">
                    <input  className="radio1" type="radio" name="gender" value="male"/> Homme<br></br>
                    </label>
                    <label className="radio">
                    <input className="radio2"type="radio" name="gender" value="female"/> Femme<br></br>
                    </label>
                    <h4 className="naissance">Date de naissance</h4>
                    <input className="date" type="date" id="date" name="date" required pattern="[0-9]{2}-[0-9]{2}-[0-9]{4}" max={date}/><br></br>
                </fieldset>
                    

                <fieldset>
                    <legend>Comment nous avez-vous connu? </legend>
                    <div className="check">
                        <input type="checkbox" id="Internet" name="Internet" checked/>
                        <label for="Internet">Internet</label>
                    </div>

                    <div className="check">
                    <input type="checkbox" id="salon" name="salon"/>
                    <label for="salon">Salon/séance d'infos</label>
                    </div>
                    <div className="check">
                    <input type="checkbox" id="amis" name="amis"/>
                    <label for="amis">Amis/Connaissances</label>
                    </div>
                    <div className="check">
                    <input type="checkbox" id="info" name="info"/>
                    <label for="info">Via un centre d'information (Info jeunes,...)</label>
                    </div>
                    <div className="check">
                    <input type="checkbox" id="ecole" name="ecole"/>
                    <label for="ecole">Via mon école/université</label>
                    </div>
                </fieldset>
                
            </form>
            </div>
        </div>
    );
}}
export default formulaire;